angular.module("app").controller('MessagesController', function($scope, $rootScope, $location, ConversationsRescource, TellerService,$ionicPopup,$ionicLoading) {
  ConversationsRescource.get({page: 1,per: 100},function(data){
    $scope.messages = data.conversations;
    $scope.isEmpty = ($scope.messages.length === 0);
  });

  $scope.setTellerName = function(name, uuid){
    TellerService.setTeller(name, uuid);
  };

  $rootScope.hideTabs = true;

  $scope.deleteNotify = function($index){
    var id = this.msg.id;
    var confirmPopup = $ionicPopup.confirm({
      template: '<div class="popup-buttons" style="text-align:center;color:#ff4128">确认删除</div>',
      buttons: [
      {
        text: '取消',
        type: 'button btn-green-dark btn-popup-sm'
      },{
        text: '确认',
        type: 'button btn-green-dark btn-popup-sm',
        onTap: function(e){
          ConversationsRescource.delete({id:id}).$promise.then(function(){
            var _remove = $scope.messages.splice($index,1);
          },function(err){
            //error
          });
        }
      }
      ]
    });
    // console.log(confirmPopup);
  };

  $scope.showConfirm = function() {
    var message = false;

    if (message) {
      var alertPopup = $ionicPopup.alert({
        template: '<div class="popup-buttons">' + message + '</div>',
        buttons: [
        {
          text: '确定',
          type: 'button btn-green-dark btn-popup-sm'
        }
        ]
      });

      alertPopup.then(function(res) {
        $scope.checked = true;
      });

    } else {
      var confirmPopup = $ionicPopup.confirm({
        template: '<div class="popup-buttons" style="text-align:center;color:#ff4128">确认删除</div>',
        buttons: [
        {
          text: '取消',
          type: 'button btn-green-dark btn-popup-sm'
        },{
          text: '确认',
          type: 'button btn-green-dark btn-popup-sm'
        }
        ]
      });
    }
  };


});
