angular.module("app").controller('CityController', function($state ,$scope, $location, $stateParams, CitiesRescource, $window) {
  $scope.children = CitiesRescource.get({id: $stateParams.id});

  $scope.pickCity = function(id, name) {
    $window.localStorage.pickedCity = name;
    $window.localStorage.pickedCityId = id;
    if ($window.localStorage.cityPicking === 'profile') {
      $window.localStorage.cityPicking = '';
      $state.go("tabs.profile", {showCustomBack: true}, {reload: true});
    } else {
      $window.localStorage.cityPicking = '';
      $state.go("tabs.map", {showCustomBack: true}, {reload: true});
    }
  };
});
