angular.module("app").controller('EventMapController', function(event, $scope, $rootScope, $location, $http, $cordovaGeolocation) {
  $scope.map = {};
  $scope.map.markers = [];

  // 朝外soho
  var longitude = 116.45263443774;
  var latitude = 39.927832216747;

  $scope.map.options = {
      center: {
          longitude: longitude,
          latitude: latitude
      },
      zoom: 17,
      city: '北京',
      width: window.innerWidth,
      height: window.innerHeight - 60
  };

  var myIcon = new BMap.Icon("img/map-icon.png", new BMap.Size(40, 40));

  var myGeo = new BMap.Geocoder();
  myGeo.getPoint(event.address, function(point){
    if (point) {
      $scope.map.handler.centerAndZoom(point, 16);
      $scope.map.handler.addOverlay(new BMap.Marker(point, {icon: myIcon}));
    } else {
      // alert("没有找到这个地址哦！");
      $rootScope.popupNotice('没有找到这个地址O<br>请与主办方联络');

    }
  });
});