angular.module("app").controller('CheckoutController',
  function($ionicTabsDelegate, $ionicLoading, $rootScope, $scope, $stateParams,$ionicPopup, $location, EventResource, $state, ticket) {

  $ionicLoading.hide();
  $rootScope.hideTabs = true;
  $scope.ticket = ticket;

  var tabPrefixs = ['you', 'hot', 'me'];
  var eventUrl = "tabs." + tabPrefixs[$ionicTabsDelegate.selectedIndex()] + "_event";

  $scope.goAlipay = function(){
    var ref = window.open($scope.ticket.alipay_wap_url, '_blank', 'location=no,closebuttoncaption=取消');
    ref.addEventListener('loadstart', function(event) {
      if (event.url.indexOf("http://owhat") === 0 || event.url.indexOf("http://www.owhat") === 0) {
        ref.close();
        EventResource.get({ id: ticket.event_id }, function(event) {
          if(event.is_paid){
//            event.is_paid
            $ionicPopup.alert({
              template: '报名成功'
            });
            // 等支付宝结果
            $state.go(eventUrl, {eventId: ticket.event_id, showCustomBack: true}, {reload: true});
          }
        });
      }
    });

    ref.addEventListener('loaderror', function(event) {
      ref.close();
      $state.go(eventUrl, {eventId: ticket.event_id, showCustomBack: true}, {reload: true});
    });
  };


});
