angular.module("app").controller('AttendeesController', function($http, event, $ionicPopup, $scope, $rootScope, $location, EventResource, $window, UserResource) {
  $scope.event = event;
  $scope.pagination = {total: 20, perSize: 20, currentPage: 0} ;
  $scope.attendees = [];
  $scope.haveMore = true;

  $scope.fetchMore = function(){
    var paginator = $scope.pagination;
    $scope.haveMore = paginator.currentPage * paginator.perSize < paginator.total;

    EventResource.attendees({id: event.id,per: paginator.perSize,page: paginator.currentPage+=1},function(data){
      paginator.total = data.count;

      for (var i = 0;  i < data.attendees.length; i++ ){
        $scope.attendees.push(data.attendees[i]);
      }
      $scope.$broadcast('scroll.infiniteScrollComplete');
      // $scope.haveMore = false;
    },function(){
      $scope.$broadcast('scroll.infiniteScrollComplete');
      // $scope.haveMore = false;
    });
  };

  $scope.myEvent = (event.user_uuid === $window.localStorage.uuid);
  $scope.myself = $window.localStorage.uuid;
  $scope.data = {};

  $scope.doRefresh = function() {
    var paginator = $scope.pagination;
    $scope.haveMore = true;

    EventResource.attendees({id: event.id,per: paginator.perSize,page: paginator.currentPage=1}, function(data){
      $scope.attendees = data.attendees;
      $scope.$broadcast('scroll.refreshComplete');
    }, function() {
      $scope.$broadcast('scroll.refreshComplete');
    });
  };

  $scope.unfollow = function(uuid, index){
    $http.post(options.api.base_url + '/users/' + uuid + '/unfollow').success(function(data){
      for(i=0;i<$scope.attendees.length;i++) {
        var a = $scope.attendees[i];
        if (a.user_uuid === uuid) {
          a.is_following = data.is_following;
        }
      }
    });
  };

  $scope.follow = function(uuid, index){
    $http.post(options.api.base_url + '/users/' + uuid + '/follow').success(function(data){
      for(i=0;i<$scope.attendees.length;i++) {
        var a = $scope.attendees[i];
        if (a.user_uuid === uuid) {
          a.is_following = data.is_following;
        }
      }
    });
  };

  $scope.exportAttendees = function() {
    var myPopup = $ionicPopup.show({
      template: '<div class="popup-buttons"><input type="email" ng-model="data.email" placeholder="输入邮箱"></div>',
      title: '我们会把报名列表生成表单发到您的邮箱',
      scope: $scope,
      buttons: [
        {
          text: '<b>发送</b>',
          type: 'button btn-green-dark btn-popup-sm',
          onTap: function(e) {
            if (!$scope.data.email) {
              //don't allow the user to close unless he enters wifi password
              e.preventDefault();
            } else {
              EventResource.export({id: event.id, email: $scope.data.email});
              $rootScope.popupNotice("已发送列表至邮箱");
              return $scope.data.email;
            }
          }
        },
        { text: '取消',type: 'button button-clear btn-popup-sm' }
      ]
    });
    myPopup.then(function(res) {
      console.log('Tapped!', res);
    });
  };
});