angular.module("app").controller('CommentsController', function($ionicPopup, $scope, $rootScope, $location, CommentResource, $stateParams, $ionicLoading) {
  $scope.comments = [];
  CommentResource.get({event_id: $stateParams.eventId},function(req){
    $scope.comments = req.comments;
  });

  $rootScope.hideTabs = true;

  $scope.submitForm = function(validate) {
    if (validate === false) { return; }
    $ionicLoading.show({ template: '加载中...' });

    var comment = new CommentResource();
    comment.content = $scope.comment.content;
    comment.event_id = $stateParams.eventId;
    comment.$save().then(function(data) {
      $scope.comment.content = '';
      $scope.comments.unshift(data);
      $ionicLoading.hide();
    }, function(error) {
      $ionicLoading.hide();

      $ionicPopup.alert({
        template: '<div class="popup-buttons">出错啦！</div>',
        buttons: [
          {
            text: '确定',
            type: 'button btn-green-dark btn-popup-sm'
          }
        ]
      });
    });
  };
});
