angular.module("app").controller('FriendsController', function($http, $scope, $rootScope, UserResource, $window) {
  $rootScope.hideTabs = true;
  $scope.friends = [];

  $scope.search = function(keyword){
    if (keyword && keyword.length > 0) {
      UserResource.search({keyword: keyword},function(data){
        $scope.friends  = data.users;
      });
      $window.localStorage.search_keyword = keyword;
    }
  };

  $scope.unfollow = function(user, index){
    $http.post(options.api.base_url + '/users/' + user.uuid + '/unfollow').success(function(user) {
      $scope.friends[index] = user;
    });
  };

  $scope.follow = function(user, index){
    $http.post(options.api.base_url + '/users/' + user.uuid + '/follow').success(function(user) {
      $scope.friends[index] = user;
    });
  };

  if (typeof $window.localStorage.search_keyword !== undefined) {
    $scope.keyword = $window.localStorage.search_keyword;
    $scope.search($scope.keyword);
  }
});
