angular.module("app").controller('PublishController', function(user, $timeout, $ionicScrollDelegate,
  $scope, $window, $location, EventResource, $cordovaCamera, $ionicActionSheet, $state,
  $ionicPopup, $ionicLoading, $ionicNavBarDelegate, $rootScope, $filter, $ionicModal, $cordovaDevice) {

  $scope.goMap = function() {
    $window.localStorage.newEvent = JSON.stringify($scope.event);
    $window.localStorage.eventData_start_date = JSON.stringify($scope.eventData.start_date);
    $window.localStorage.eventData_start_time = JSON.stringify($scope.eventData.start_time);
    $window.localStorage.eventData_end_time   = JSON.stringify($scope.eventData.end_time);
    $state.go("tabs.map");
  };

  $scope.goBack = function() {
    $window.localStorage.newEvent = JSON.stringify($scope.event);

    $ionicNavBarDelegate.back();
  };

  $scope.coverImage1 = "img/publish-addimg.jpg";
  $scope.coverImage2 = "img/publish-addimg.jpg";
  $scope.coverImage3 = "img/publish-addimg.jpg";

  $rootScope.hideTabs = true;

  $scope.saleFormat = true;

  $scope.eventData = {};

  if ($window.localStorage.eventData_start_date) {
    $scope.eventData.start_date = JSON.parse($window.localStorage.eventData_start_date);
  } else {
    $scope.eventData.start_date = moment().format("YYYY-MM-DD");
  }
  if ($window.localStorage.eventData_start_time) {
    $scope.eventData.start_time = JSON.parse($window.localStorage.eventData_start_time);
  } else {
    $scope.eventData.start_time = moment().format("HH:mm");
  }
  if ($window.localStorage.eventData_end_time) {
    $scope.eventData.end_time = JSON.parse($window.localStorage.eventData_end_time);
  } else {
    $scope.eventData.end_time = moment().format("HH:mm");
  }

  if ($window.localStorage.newEvent) {
    $scope.event = JSON.parse($window.localStorage.newEvent);
    $scope.saleFormat = $scope.event.ticket_limit;
    if ($scope.event.cover_data1 && $scope.event.cover_data1.length > 0) {
      $scope.coverImage1 = "data:image/jpeg;base64," + $scope.event.cover_data1;
    }
    if ($scope.event.cover_data2 && $scope.event.cover_data2.length > 0) {
      $scope.coverImage2 = "data:image/jpeg;base64," + $scope.event.cover_data2;
    }
    if ($scope.event.cover_data3 && $scope.event.cover_data3.length > 0) {
      $scope.coverImage3 = "data:image/jpeg;base64," + $scope.event.cover_data3;
    }
  } else {
    $scope.event = {
      free: true,
      sync_weibo: true,
      sync_qq: true
    };
  }

  $scope.$watch("event.free", function(newV, oldV) {
    if (newV === false && !user.verified_at) {
      $rootScope.popupNotice("非认证用户发不了收费活动哦，如需申请认证，请您到我们的官网上查询申请事宜。");
      $timeout(function() {
        $scope.event.free = true;
      }, 300);
      return;
    }

    if (newV === true) {
      $scope.event.fee = null;
      $scope.event.original_fee = null;
    }
  });

  if ($window.localStorage.mapLon) {
    $scope.event.longitude = $window.localStorage.mapLon;
    delete($window.localStorage.mapLon);
  }

  if ($window.localStorage.mapAddress) {
    $scope.event.address = $window.localStorage.mapAddress;
    delete($window.localStorage.mapAddress);
  }

  if ($window.localStorage.mapLat) {
    $scope.event.latitude = $window.localStorage.mapLat;
    delete($window.localStorage.mapLat);
  }

  $scope.updateEditor = function() {
    var element = document.getElementById("event_desc");
    element.style.height = element.scrollHeight + "px";
  };

  $scope.publishPopupUndone = function(){
    var publishPopup = $ionicPopup.show({
      template: '<div class="forget-popup">' +
      '<img class="user-photo pull-left" src="img/icon-insectcry-gray.png" alt="" />' +
      '<div class="messages">' +
      '<span class="block">还没完成呢！</span>' +
      '要丢进草稿窝吗？' +
      '</div>' +
      '</div>',
      scope: $scope,
      title: false,
      buttons: [
      {
        text: '想想',
        type: 'btn-popup-sm btn-green-dark'
      },
      {
        text: '丢',
        type: 'btn-popup-sm button-clear'
      }
      ]
    });
  };

  var publishPopupSuccess = function(){
    var publishPopup = $ionicPopup.show({
      template: '<div class="forget-popup text-center"><h4><img class="user-photo user-photo-sm" src="img/icon-question.png" alt="" /> 已告知所有O星人</h4> <div>可以去”O！我”里找到该活动</div></div>',
      scope: $scope,
      title: false,
      buttons: [
      {
        text: '得意一戳',
        type: 'btn-popup-sm btn-green-dark',
        onTap: function(e) {
          $ionicNavBarDelegate.back();
        }
      }
      ]
    });
  };

  $scope.submitForm = function(validate) {
    if (validate === false) { return; }

    if (!$scope.event.free && !user.verified_at) {
      $rootScope.popupNotice("非认证用户发不了收费活动哦，如需申请认证，请您到我们的官网上查询申请事宜。");
      return;
    }

    $ionicLoading.show({ template: '加载中...' });

    var event = new EventResource();
    event.title = $scope.event.title;
    event.description = $scope.event.description;
    event.ticket_total = $scope.event.ticket_total;
    event.address = $scope.event.address;
    event.longitude = $scope.event.longitude;
    event.latitude = $scope.event.latitude;
    event.mobile = $scope.event.mobile;
    event.cover_data1 = $scope.event.cover_data1;
    event.cover_data2 = $scope.event.cover_data2;
    event.cover_data3 = $scope.event.cover_data3;

    event.fee = $scope.event.fee;
    event.original_fee = $scope.event.original_fee;
    event.free = $scope.event.free;

    var start_at = $scope.eventData.start_date + " " + $scope.eventData.start_time;
    var end_at = $scope.eventData.start_date + " " + $scope.eventData.end_time;

    event.start_at = moment(start_at).toDate();
    event.end_at = moment(end_at).toDate();
    event.ticket_limit = $scope.event.ticket_limit;
    event.sync_weibo = $scope.event.sync_weibo;
    event.sync_qq = $scope.event.sync_qq;
    event.ext_info = $scope.event.ext_info;
    event.reg_fields = $scope.selectedRegFields();

    event.$save().then(function(data) {
      $ionicLoading.hide();
      publishPopupSuccess();
      delete($window.localStorage.newEvent);
    }, function(error) {
      $ionicLoading.hide();
      $ionicPopup.alert({
        template: '<div class="popup-buttons">出错啦！</div>',
        buttons: [
          {
            text: '确定',
            type: 'button btn-green-dark btn-popup-sm'
          }
        ]
      });
    });
  };

  $scope.takePicture = function(num) {
    var picSheet = $ionicActionSheet.show({
      buttons: [
      { text: '拍照' },
      { text: '从手机相册选择' }
      ],
      cancelText: '取消',
      buttonClicked: function(index) {
        if (index === 0) {
          sourceType = Camera.PictureSourceType.CAMERA;
        } else {
          sourceType = Camera.PictureSourceType.PHOTOLIBRARY;
        }

        var options = {
          quality : 50,
          destinationType : Camera.DestinationType.DATA_URL,
          allowEdit : true,
          sourceType : sourceType,
          encodingType: Camera.EncodingType.JPEG,
          targetWidth: 1280,
          targetHeight: 892,
          popoverOptions: CameraPopoverOptions,
          saveToPhotoAlbum: false
        };
        $cordovaCamera.getPicture(options).then(function(imageData) {
          if (num === 1) {
            $scope.event.cover_data1 = imageData;
            $scope.coverImage1 = "data:image/jpeg;base64," + $scope.event.cover_data1;
          }
          if (num === 2) {
            $scope.event.cover_data2 = imageData;
            $scope.coverImage2 = "data:image/jpeg;base64," + $scope.event.cover_data2;
          }
          if (num === 3) {
            $scope.event.cover_data3 = imageData;
            $scope.coverImage3 = "data:image/jpeg;base64," + $scope.event.cover_data3;
          }
        }, function(err) {
          $ionicPopup.alert({
            template: '<div class="popup-buttons">出错啦！</div>',
            buttons: [
              {
                text: '确定',
                type: 'button btn-green-dark btn-popup-sm'
              }
            ]
          });
        });
        return true;
      }
    });
  };

  $scope.regFields = [
    {id: "name", text: "姓名", checked: true },
    {id: "phone", text: "电话", checked: true },
    {id: "gender", text: "性别", checked: true },
    {id: "email", text: "邮件", checked: true }
  ];

  // helper method to get selected fruits
  $scope.selectedRegFields = function() {
    var selected =  $filter('filter')($scope.regFields, { checked: true });
    return selected.map(function(e){return e.id;}).toString();
  };

  $scope.isAndroid = $cordovaDevice.getPlatform() === 'Android';

  $scope.pickDate = function() {

    if (!$scope.isAndroid) {
      return;
    }
    var options = {
      date: moment($scope.eventData.start_date).toDate(),
      mode: 'date'
    };
    datePicker.show(options, function(date){
      if (date.toString() === "Invalid Date") {
        return;
      }
      $scope.eventData.start_date = moment(date).format("YYYY-MM-DD");
    });
  };

  $scope.pickTime = function(type) {
    if (!$scope.isAndroid) {
      return;
    }

    var options;

    if (type === 'start') {
      options = {
        date: moment($scope.eventData.start_date + " " + $scope.eventData.start_time).toDate(),
        mode: 'time'
      };
      datePicker.show(options, function(time){
        if (time.toString() === "Invalid Date") {
          return;
        }
        $scope.eventData.start_time = moment(time).format("HH:mm");

      });
    } else {
      options = {
        date: moment($scope.eventData.start_date + " " + $scope.eventData.end_time).toDate(),
        mode: 'time'
      };
      datePicker.show(options, function(time){
        if (time.toString() === "Invalid Date") {
          return;
        }
        $scope.eventData.end_time = moment(time).format("HH:mm");

      });
    }

  };
});