angular.module("app").controller('TicketsController', function($scope, $location, $ionicPopup, TicketResource, $timeout, $window) {
  $scope.isEmpty = false;
  TicketResource.get().$promise.then(function(res){
   $scope.tickets = res.tickets;
   $scope.isEmpty = ($scope.tickets.length === 0);
  },function(res){
    $scope.tickets = [];
    $scope.isEmpty = true;
   //handle err
  });

  $window.addEventListener('click', function(event) {
    if (!$scope.showPop && $scope.myPopup) {
      $scope.myPopup.close();
      $scope.showPop = true;
    }
  });

  $scope.showPop = true;
  $scope.showPopup = function($index) {
    var imgUrl = $scope.tickets[$index].imgcode;
    $timeout(function() {
      $scope.myPopup = $ionicPopup.show({
        template: '<p class="text-center pop-code-bg"><img src="' + imgUrl + '" /></p>',
        // title: 'Enter Wi-Fi Password',
        // subTitle: 'Please use normal things',
        scope: $scope,
        buttons: [
          // { text: 'Cancel' },
          {
            text: '这货就是门票，入场扫一扫O！',
            type: 'button-assertive pop-imgcode',
            onTap: function() {
              $scope.showPop = true;
            }
          }
        ]
      });
      $scope.showPop = false;
    }, 50);
  };

});
