angular.module("app").controller('MapController', function(EventResource, $timeout, $stateParams, $ionicNavBarDelegate, $scope, $rootScope, $window, $location, $http, $cordovaGeolocation, $ionicModal, $state, AreasRescource, CitiesRescource) {

  $scope.goBack = function(data) {
    if (data.location && data.location.length > 0){
      $window.localStorage.mapAddress = data.location;
      $window.localStorage.mapLon = data.longitude;
      $window.localStorage.mapLat = data.latitude;
    }
    $ionicNavBarDelegate.back();
  };

  $scope.pickCity = function(){
    $window.localStorage.cityPicking = 'publish';
    $state.go("tabs.areas", {}, {location: 'replace'});
  };

  $scope.pickProvince = function(id, name) {
    $scope.provinceId = id;
    $scope.provinceName = name;
    $scope.closeModal("province");
    $scope.openModal("city");
  };
  $scope.pickCity = function(id, name) {
    $scope.cityId = id;
    $scope.cityName = name;
    $scope.data.city = $scope.provinceName + $scope.cityName;
    $scope.closeModal("city");
  };

  $scope.data = {};
  $scope.data.city = "";
  $scope.data.location = "";
  $scope.data.latitude = "";
  $scope.data.longitude = "";

  $scope.map = {};
  $scope.map.markers = [];
  $rootScope.hideTabs = true;

  // 朝外soho
  var longitude = 116.45263443774;
  var latitude = 39.927832216747;

  var myIcon = new BMap.Icon("img/map-icon.png", new BMap.Size(40, 40));

  $scope.search = function() {
    var myGeo = new BMap.Geocoder();
    myGeo.getPoint($scope.data.location, function(point){
      if (point) {
        $scope.map.handler.centerAndZoom(point, 16);
        $scope.map.handler.addOverlay(new BMap.Marker(point, {icon: myIcon}));
        console.log(point);
        $scope.data.longitude = point.lng;
        $scope.data.latitude = point.lat;
      } else {
        $scope.data.longitude = null;
        $scope.data.latitude = null;
        // alert("没有找到您输入的地址哦！");
        $rootScope.popupNotice('没有找到您输入的地址哦！');
      }
    }, $scope.data.city);
  };

  $scope.map.options = {
    center: {
      longitude: longitude,
      latitude: latitude
    },
    city: '北京',
    zoom: 17,
    width: window.innerWidth,
    height: window.innerHeight - 110
  };

  if ($stateParams.id) {
    EventResource.get({id: $stateParams.id}, function(event) {
      var myGeo = new BMap.Geocoder();
      myGeo.getPoint(event.address, function(point){
        if (point) {
          $scope.map.handler.centerAndZoom(point, 16);
          $scope.map.handler.addOverlay(new BMap.Marker(point));
        }
      }, $scope.data.city);
    });
  }
});