angular.module("app").controller('ForgetController', function($ionicPopup, $scope, $interval, $location, $rootScope, $http,
  UserResource, $timeout, $state, $window) {
  $scope.user = {};
  $scope.codeSent = false;
  $scope.codeSentBtnText = "发送验证码";
  $scope.readSenconds = 120;

  $scope.sendSmsCode = function() {
    $scope.codeSent = true;
    $timeout(function() {
      $scope.codeSent = false;
    }, 120000);

    $http.post(options.api.base_url + '/auth/reset_code', {"mobile": $scope.user.mobile});

    $scope.codeSentBtnText = $scope.readSenconds + "秒后重新发送";
    $scope.stop = $interval(function () {
        if ($scope.readSenconds > 1) {
          $scope.readSenconds = $scope.readSenconds - 1;
          $scope.codeSentBtnText = $scope.readSenconds + "秒后重新发送";
        }else{
          $scope.codeSentBtnText = "重新发送验证码";
          $scope.readSenconds = 120;
        }
    }, 1000, 120);
  };

  $scope.submitForm = function() {
    var user = new UserResource();
    user.mobile = $scope.user.mobile;
    user.password = $scope.user.password;
    user.code = $scope.user.code;
    user.$updatePassword().then(function() {
      $window.localStorage.uuid = user.uuid;
      $window.localStorage.token = user.token;
      $state.go("tabs.hot");
    }, function() {
      $ionicPopup.alert({
        template: '<div class="popup-buttons">出错啦！</div>',
        buttons: [
          {
            text: '确定',
            type: 'button btn-green-dark btn-popup-sm'
          }
        ]
      });
    });
  };
});
