angular.module("app").controller('ProfileController', function($stateParams, $scope, $rootScope, $state, $cordovaCamera,
    $ionicActionSheet, $window, MeResource, UserResource, $ionicPopup, $timeout, $http) {
  $scope.user = {};
  $scope.weiboBinded = false;
  $scope.qqBinded = false;

  function checkIdentities() {
    for (var i = $scope.user.identities.length - 1; i >= 0; i--) {
      if($scope.user.identities[i].provider === 'weibo') {
        $scope.weiboBinded = true;
      }

      if($scope.user.identities[i].provider === 'qq_connect') {
        $scope.qqBinded = true;
      }
    }
  }

  $scope.showCustomBack = $stateParams.showCustomBack;
  $scope.user = MeResource.get(function(user){
    $scope.avatar = user.avatar;
    $scope.mobile = user.mobile;
    $scope.user.avatarChanged = false;
    if($window.localStorage.pickedCity) {
      $scope.area_name = $window.localStorage.pickedProvince + ', ' + $window.localStorage.pickedCity;
    } else {
      $scope.area_name = user.area_name;
    }
    checkIdentities();
  });

  $scope.pickCity = function(){
    $window.localStorage.cityPicking = 'profile';
    $state.go("tabs.areas", {}, {location: 'replace'});
  };

  $scope.takePicture = function() {
    var picSheet = $ionicActionSheet.show({
      buttons: [
        { text: '拍照' },
        { text: '从手机相册选择' }
      ],
      cancelText: '取消',
      buttonClicked: function(index) {
        if (index === 0) {
          sourceType = Camera.PictureSourceType.CAMERA;
        } else {
          sourceType = Camera.PictureSourceType.PHOTOLIBRARY;
        }

        var options = {
          quality : 50,
          destinationType : Camera.DestinationType.DATA_URL,
          allowEdit : true,
          sourceType : sourceType,
          encodingType: Camera.EncodingType.JPEG,
          targetWidth: 200,
          targetHeight: 200,
          popoverOptions: CameraPopoverOptions,
          saveToPhotoAlbum: false
        };
        $cordovaCamera.getPicture(options).then(function(imageData) {
          $scope.user.avatar_data = imageData;
          $scope.avatar = "data:image/jpeg;base64," + $scope.user.avatar_data;
        }, function(err) {
          // An error occured. Show a message to the user
        });
        return true;
      }
    });
  };

  $scope.submitForm = function(validate){
    if (!validate) { return; }

    if ($window.localStorage.pickedCityId !== undefined && $window.localStorage.pickedCityId.length > 0) {
      $scope.user.area_id = $window.localStorage.pickedCityId;
    }
    $scope.user.$update().then(function(user) {
      // $scope.user = user;
      // checkIdentities();
      $state.go('tabs.me', {}, {reload: true});
    }, function(error) {
      $rootScope.popupNotice(error.data.detail);
    });

    delete $window.localStorage.pickedCity;
    delete $window.localStorage.pickedCityId;
    delete $window.localStorage.pickedProvince;
  };

  $scope.parser = document.createElement('a');
  var oauthBind= function (provider) {
    var url = "";
    if (provider === 'weibo') {
      url = "https://api.weibo.com/oauth2/authorize?client_id=337167425&response_type=code&redirect_uri=http%3A%2F%2Fwww.owhat.cn/auth/weibo/callback404&state=app";
    } else {
      url = "https://graph.qq.com/oauth2.0/authorize?client_id=101138496&redirect_uri=http%3A%2F%2Fwww.owhat.cn/404.html&response_type=code&state=app";
    }

    var ref = window.open(url, '_blank', 'location=no,closebuttoncaption=取消');
    ref.addEventListener('loadstart', function(event) {
      if (event.url.indexOf('http://www.owhat') === 0) {
        ref.close();
        $scope.parser.href = event.url;
        $scope.$apply(function(scope){
          code = $scope.parser.search.split("code=")[1].split("&")[0];
          if (code) {
            var callback_url = options.api.base_url + '/auth/' + provider + '/callback?state=app&code=' + code;
            $http.get(callback_url).success(function(data) {
              if (provider === 'weibo') {
                scope.weiboBinded = true;
              } else {
                scope.qqBinded = true;
              }
              // bindsnsPop();
            }).error(function() {
              $ionicPopup.alert({
                template: '<div class="popup-buttons">出错啦！</div>',
                buttons: [
                  {
                    text: '确定',
                    type: 'button btn-green-dark btn-popup-sm'
                  }
                ]
              });
            });
          }
        });
      }
    });
  };

  var oauthUnBind = function(provider) {
    var url = options.api.base_url + '/auth/unbind/' + provider + '?state=app';
    $http.post(url).success(function(data) {
      if (provider === 'weibo') {
        $scope.weiboBinded = false;
      } else {
        $scope.qqBinded = false;
      }
    }).error(function(error) {
      $rootScope.popupNotice("解绑失败！");
    });
  };

  $scope.weiboUnBind= function() {
    oauthUnBind('weibo');
  };

  $scope.qqUnBind= function() {
    oauthUnBind('qq_connect');
  };

  $scope.weiboBind = function() {
    window.socialmessage.weiboLogin(function(data) {
      var uid = data.usid;
      var token = data.accessToken;
      var tokenUrl = options.api.base_url + '/auth/weibo/save_token';
      $http.post(tokenUrl, {uid: uid, token: token}).success(function(user) {
        $scope.weiboBinded = true;
      }).error(function(error) {

      });
    }, function(error) {

    });
  };

  $scope.qqBind = function() {
    // oauthLogin('qq_connect');
    // $ionicLoading.show({ template: '加载中...' });
    window.socialmessage.qqLogin(function(data) {
      var uid = data.usid;
      var token = data.accessToken;
      var tokenUrl = options.api.base_url + '/auth/qq_connect/save_token';
      // alert(uid);
      // alert(token);
      $http.post(tokenUrl, {uid: uid, token: token}).success(function(user) {
        $scope.qqBinded = true;
      }).error(function(error) {

      });
    }, function(error) {

    });
  };

  var bindsnsPop = function() {
    // 绑定成功...
    var bindSuccessPop = $ionicPopup.show({
      template: '<div class="text-center">绑定成功</div>',
      title: '',
      subTitle: '',
      scope: $scope,
      buttons: ''
    });
    $timeout(function() {
       bindSuccessPop.close(); //close the popup after 3 seconds for some reason
    }, 3000);
  };
});
