app.factory('AuthenticationService', function() {
  var auth = {
    isAuthenticated: false,
    isAdmin: false
  };

  return auth;
});

app.factory('TokenInterceptor', function ($q, $window, $location, AuthenticationService) {
  return {
    request: function (config) {
      config.headers = config.headers || {};
      if (config.url.indexOf("http://api.map.baidu.com") !== 0 && $window.localStorage.token) {
        config.headers['X-User-UUID'] = $window.localStorage.uuid;
        config.headers['X-User-Token'] = $window.localStorage.token;
      }
      return config;
    },

    requestError: function(rejection) {
      return $q.reject(rejection);
    },

    /* Set Authentication.isAuthenticated to true if 200 received */
    response: function (response) {
      if (response != null && response.status === 200 && $window.localStorage.token && !AuthenticationService.isAuthenticated) {
        AuthenticationService.isAuthenticated = true;
      }
      return response || $q.when(response);
    },

    /* Revoke client authentication if 401 is received */
    responseError: function(rejection) {
      if (rejection != null && (rejection.status === 403 || rejection.status === 302 || rejection.status === 500 || rejection.status === 401 || rejection.status === 404)) {
        delete $window.localStorage.uuid;
        delete $window.localStorage.token;
        AuthenticationService.isAuthenticated = false;
        $location.path("/start/login");
      }

      return $q.reject(rejection);
    }
  };
});
