angular.module("app").controller('SettingsController', function(user, $scope,
  $rootScope, $window, $location, AuthenticationService, UserResource,
  $ionicPopup, $cordovaDevice) {

  $scope.logoutConfirm = function() {
    var logoutConfirmPopup = $ionicPopup.confirm({
      template: '<div class="text-center">是否要退出登录？</div>',
      okText: '确定',
      okType: 'btn-popup-sm btn-green-dark',
      cancelText: '取消',
      cancelType: 'btn-popup-sm btn-green-dark'
    });
    logoutConfirmPopup.then(function(res) {
      if(res) {
        $scope.logout();
      } else {

      }
    });
  };

  $scope.goFeedback = function() {
    // var ref = window.open("http://www.owhat.cn/go/feedback", '_blank', 'location=no,closebuttoncaption=取消');
    if ($cordovaDevice.getPlatform() !== "Android") {
      window.socialmessage.feedback({email: user.email, nickname: user.nickname});
    } else {
      appAvailability.feedback(function() {

      }, function () {

      });
    }
  };

  $scope.goAbout = function() {
    var ref = window.open("http://www.owhat.cn/go/about", '_blank', 'location=no,closebuttoncaption=取消');
  };

  $scope.logout = function() {
    delete $window.localStorage.uuid;
    delete $window.localStorage.token;
    delete $window.localStorage.aps_token_posted;
    AuthenticationService.isAuthenticated = false;

    UserResource.logout(function(){
      $location.path("/start/login");
    });
  };

  $scope.user = user;
  $scope.updateUser = function() {
    $scope.user.$update().then(function(data){
      $scope.user.notify_message = user.notify_message;
    });
  };
});
