angular.module("app").controller('PersonController', function($ionicTabsDelegate, $scope, $rootScope, $ionicPopup, $location, UserResource, EventResource, $stateParams, $ionicLoading, $window) {
  $rootScope.hideTabs = false;
  $scope.haveMore = true;
  $scope.showSetting = false;
  $scope.pagination = {perSize: 10, currentPage: 1};
  $scope.events = [];

  if ($ionicTabsDelegate.selectedIndex() === 2) {
    $scope.showSetting = true;
  }

  $scope.title = "";

  var userId;
  if ($stateParams.userId) {
    userId = $stateParams.userId;
  } else {
    userId = $window.localStorage.uuid;
  }

  $scope.user = {};

  $ionicLoading.show({ template: '加载中...' });
  $scope.user = UserResource.get({id: userId}, function(user) {
    $scope.title = user.nickname;
    $rootScope.isMe = ($window.localStorage.uuid === user.uuid);
    $scope.personName = $scope.isMe ? "我" : 'TA';
    $scope.isFollowing = $scope.user.is_following;
    $scope.hasUnread = user.unreadMessages > 0 || user.unreadActivities > 0 || user.unreadFollowers > 0;
    $window.localStorage.unreadMessages = user.unreadMessages;
    $window.localStorage.unreadActivities = user.unreadActivities;
    $window.localStorage.unreadFollowers = user.unreadFollowers;
    $ionicLoading.hide();
  }, function() {
    $ionicLoading.hide();
  });

  // EventResource.get({user_id: userId, per: $scope.pagination.perSize, page: 1}, function(data) {
  //   $scope.events = data.events;
  // });

  $scope.moreDataCanBeLoaded = function(){
    return $scope.haveMore;
  };

  $scope.fetchMore = function(){
    EventResource.get({user_id: userId, per: $scope.pagination.perSize, page: $scope.pagination.currentPage}, function(data) {
      $scope.events.push.apply($scope.events, data.events);

      $scope.$broadcast('scroll.infiniteScrollComplete');
      $scope.haveMore = false;
      $scope.haveMore = $scope.pagination.currentPage * $scope.pagination.perSize < data.count;
      $scope.pagination.currentPage += 1;
    }, function() {
      $scope.$broadcast('scroll.infiniteScrollComplete');
    });
  };

  $scope.eventClass = function(event) {
    if(moment(event.start_at) < moment()) {
      return "over";
    }
  };

  $scope.follow = function() {
    if ($scope.user.is_following === true) {
      UserResource.unfollow({id: $scope.user.uuid}, null, function(data) {
        $scope.user = data;
        $scope.isFollowing = $scope.user.is_following;
      });
    } else {
      UserResource.follow({id: $scope.user.uuid}, null, function(data) {
        $scope.user = data;
        $scope.isFollowing = $scope.user.is_following;
      });
    }
  };
});