angular.module("app").controller('EventEditController', function(user, $rootScope, $window, $scope, $stateParams, $location, EventResource, $cordovaCamera, $ionicActionSheet, $state, $ionicPopup, $ionicLoading, $ionicNavBarDelegate, $filter, $ionicModal, $cordovaDevice) {
  $scope.goMap = function() {
    $state.go("tabs.hot_map", {id: $stateParams.eventId});
  };

  $scope.coverImage1 = "img/publish-addimg.jpg";
  $scope.coverImage2 = "img/publish-addimg.jpg";
  $scope.coverImage3 = "img/publish-addimg.jpg";

  $scope.saleFormat = true;
  $scope.regFields = [
    {id: "name", text: "姓名", checked: true },
    {id: "phone", text: "电话", checked: true },
    {id: "gender", text: "性别", checked: true },
    {id: "email", text: "邮件", checked: true }
  ];

  $scope.event = EventResource.get({id: $stateParams.eventId}, function(event) {
    if (event.cover1.length > 0) {
      $scope.coverImage1 = event.cover1;
    }
    if (event.cover2.length > 0) {
      $scope.coverImage2 = event.cover2;
    }
    if (event.cover3.length > 0) {
      $scope.coverImage3 = event.cover3;
    }
    $scope.saleFormat = event.ticket_limit;

    var start = event.start_at.split(/T|\+/);
    $scope.event.start_date = moment(event.start_at).format("YYYY-MM-DD");
    $scope.event.start_time = moment(event.start_at).format("HH:mm");

    var end = event.end_at.split(/T|\+/);
    $scope.event.end_time = moment(event.end_at).format("HH:mm");

    for (i=0;i<$scope.regFields.length;i++) {
      var el = $scope.regFields[i];

      if (event.reg_fields.indexOf(el.id.toString()) === -1) {
        el.checked = false;
      }
    }

    if ($window.localStorage.mapLon !== undefined) {
      $scope.event.longitude = $window.localStorage.mapLon;
    }
    if ($window.localStorage.mapAddress !== undefined) {
      $scope.event.address = $window.localStorage.mapAddress;
    }
    if ($window.localStorage.mapLat !== undefined) {
      $scope.event.latitude = $window.localStorage.mapLat;
    }

    $scope.updateEditor();

    delete($window.localStorage.mapLon);
    delete($window.localStorage.mapAddress);
    delete($window.localStorage.mapLat);
  });

  $scope.$watch("event.free", function(newV, oldV) {
    if (newV === false && !user.verified_at) {
      $rootScope.popupNotice("非认证用户发不了收费活动哦，如需申请认证，请您到我们的官网上查询申请事宜。");
      $timeout(function() {
        $scope.event.free = true;
      }, 300);
      return;
    }

    if (newV === true) {
      $scope.event.fee = null;
      $scope.event.original_fee = null;
    }
  });

  $scope.updateEditor = function() {
    var element = document.getElementById("event_desc");
    element.style.height = element.scrollHeight + "px";
  };

  $scope.publishPopupUndone = function(){
    var publishPopup = $ionicPopup.show({
      template: '<div class="forget-popup">' +
        '<img class="user-photo pull-left" src="img/icon-insectcry-gray.png" alt="" />' +
        '<div class="messages">' +
        '<span class="block">还没完成呢！</span>' +
        '要丢进草稿窝吗？' +
        '</div>' +
        '</div>',
      scope: $scope,
      title: false,
      buttons: [
        {
          text: '想想',
          type: 'btn-popup-sm btn-green-dark'
        },
        {
          text: '丢',
          type: 'btn-popup-sm button-clear'
        }
      ]
    });
  };

  var publishPopupSuccess = function(){
    var publishPopup = $ionicPopup.show({
      template: '<div class="forget-popup text-center"><h4><img class="user-photo user-photo-sm" src="img/icon-question.png" alt="" /> 已告知所有O星人</h4> <div>可以去”O！我”里找到该活动</div></div>',
      scope: $scope,
      title: false,
      buttons: [
        {
          text: '得意一戳',
          type: 'btn-popup-sm btn-green-dark',
          onTap: function(e) {
            $ionicNavBarDelegate.back();
          }
        }
      ]
    });
  };

  $scope.submitForm = function(validate) {
    if (validate === false) { return; }

    if (!$scope.event.free && !user.verified_at) {
      $rootScope.popupNotice("非本星球生物不能发收银子的活动哟，欢迎来我们的官方网站申请O认证...");
      return;
    }

    $ionicLoading.show({ template: '加载中...' });

    if ($scope.event.coverData) {
      $scope.event.cover_data = $scope.event.coverData;
    }

    $scope.event.start_at = $scope.event.start_date.toString() + " " + $scope.event.start_time.toString();
    $scope.event.end_at = $scope.event.start_date.toString() + " " + $scope.event.end_time.toString();
    $scope.event.ticket_limit = $scope.saleFormat;
    $scope.event.reg_fields = $scope.selectedRegFields();

    if ($scope.event.free === true) {
      $scope.event.original_fee = 0;
      $scope.event.fee = 0;
    }

    delete($scope.event.cover);
    delete($scope.event.cover1);
    delete($scope.event.cover2);
    delete($scope.event.cover3);
    delete($scope.event.start_date);
    delete($scope.event.start_time);
    delete($scope.event.end_time);
    delete($scope.event.attendees);
    delete($scope.event.buyable);
    delete($scope.event.share_text);
    delete($scope.event.share_url);
    delete($scope.event.user_uuid);
    delete($scope.event.user_avatar);
    delete($scope.event.comments_count);
    delete($scope.event.user_nickname);
    delete($scope.event.is_paid);
    delete($scope.event.ticket_limit);

    $scope.event.$save({}).then(function(data) {
      $ionicLoading.hide();
      publishPopupSuccess();
      delete($window.localStorage.mapLon);
      delete($window.localStorage.mapAddress);
      delete($window.localStorage.mapLat);
    }, function(error) {
      var start = $scope.event.start_at.split(' ');
      $scope.event.start_date = start[0];
      $scope.event.start_time = start[1];

      var end = $scope.event.end_at.split(' ');
      $scope.event.end_time = end[1];

      $ionicLoading.hide();
      
      $ionicPopup.alert({
        template: '<div class="popup-buttons">出错啦！</div>',
        buttons: [
          {
            text: '确定',
            type: 'button btn-green-dark btn-popup-sm'
          }
        ]
      });
    });
  };

  $scope.takePicture = function(num) {
    var picSheet = $ionicActionSheet.show({
      buttons: [
        { text: '拍照' },
        { text: '从手机相册选择' }
      ],
      cancelText: '取消',
      buttonClicked: function(index) {
        if (index === 0) {
          sourceType = Camera.PictureSourceType.CAMERA;
        } else {
          sourceType = Camera.PictureSourceType.PHOTOLIBRARY;
        }

        var options = {
          quality : 50,
          destinationType : Camera.DestinationType.DATA_URL,
          allowEdit : true,
          sourceType : sourceType,
          encodingType: Camera.EncodingType.JPEG,
          targetWidth: 1280,
          targetHeight: 892,
          popoverOptions: CameraPopoverOptions,
          saveToPhotoAlbum: false
        };
        $cordovaCamera.getPicture(options).then(function(imageData) {
          if (num === 1) {
            $scope.event.cover_data1 = imageData;
            $scope.coverImage1 = "data:image/jpeg;base64," + $scope.event.cover_data1;
          }
          if (num === 2) {
            $scope.event.cover_data2 = imageData;
            $scope.coverImage2 = "data:image/jpeg;base64," + $scope.event.cover_data2;
          }
          if (num === 3) {
            $scope.event.cover_data3 = imageData;
            $scope.coverImage3 = "data:image/jpeg;base64," + $scope.event.cover_data3;
          }
        }, function(err) {
          $ionicPopup.alert({
            template: '<div class="popup-buttons">出错啦！</div>',
            buttons: [
              {
                text: '确定',
                type: 'button btn-green-dark btn-popup-sm'
              }
            ]
          });
        });
        return true;
      }
    });
  };

  // helper method to get selected fruits
  $scope.selectedRegFields = function() {
    var selected =  $filter('filter')($scope.regFields, { checked: true });
    return selected.map(function(e){return e.id;}).toString();
  };

  $scope.isAndroid = $cordovaDevice.getPlatform() === 'Android';

  $scope.pickDate = function() {

    if (!$scope.isAndroid) {
      return;
    }
    var options = {
      date: moment($scope.event.start_date).toDate(),
      mode: 'date'
    };
    datePicker.show(options, function(date){
      if (date.toString() === "Invalid Date") {
        return;
      }
      $scope.event.start_date = moment(date).format("YYYY-MM-DD");
    });
  };

  $scope.pickTime = function(type) {
    if (!$scope.isAndroid) {
      return;
    }

    var options;

    if (type === 'start') {
      options = {
        date: moment($scope.event.start_date + " " + $scope.event.start_time).toDate(),
        mode: 'time'
      };
      datePicker.show(options, function(time){
        if (time.toString() === "Invalid Date") {
          return;
        }
        $scope.event.start_time = moment(time).format("HH:mm");

      });
    } else {
      options = {
        date: moment($scope.event.start_date + " " + $scope.event.end_time).toDate(),
        mode: 'time'
      };
      datePicker.show(options, function(time){
        if (time.toString() === "Invalid Date") {
          return;
        }
        $scope.event.end_time = moment(time).format("HH:mm");

      });
    }

  };
});