angular.module("app").controller('AreaController', function($state, $scope, $location, AreasRescource, $window) {
  $scope.areas = AreasRescource.query();

  $scope.pickCity = function(id, name) {
    $window.localStorage.pickedProvince = name;
    $state.go("tabs.cities", {id: id}, {location: 'replace'});
  };
});
