angular.module("app").controller('MessageController', function($interval, $http, $window, $scope, $rootScope, $stateParams, $location, ConversationsRescource, $ionicScrollDelegate) {
  $scope.content = '';
  $scope.isEmpty = false;
  $scope.loading = false;
  $scope.glued = true;

  ConversationsRescource.messages({id: $stateParams.userId}, function(data){
    $scope.teller = data.teller;
    $scope.me = data.user;
    $scope.messages = data.messages;

    $scope.isEmpty = ($scope.messages.length === 0);

    $window.localStorage.unreadMessages -= data.unread;
    $ionicScrollDelegate.scrollBottom(true);

    $rootScope.intervalTimer = $interval(function(){
      if ($scope.loading === false) {
        $scope.loading = true;

        var last = $scope.messages[$scope.messages.length - 1];
        var id = 0;
        if (last) {
          id = last.id;
        }

        ConversationsRescource.messages({id: $stateParams.userId, after: id}, function(data){
          data.messages.forEach(function(el){
            el.read = true;
            $scope.messages.push(el);
          });
          $scope.loading = false;
          $ionicScrollDelegate.scrollBottom(true);
        });
      }
    }, 5000);
  });

  $rootScope.$on("$locationChangeStart", function(event, next, current) {
    $interval.cancel($rootScope.intervalTimer);
  });

  $scope.sendMessage = function(){
    $scope.loading = true;
    if ($scope.content === '') {
      $rootScope.popupNotice("还没输入O");
      return;
    }

    $http.post(options.api.base_url + '/messages', {user_id: $scope.teller.uuid, content: $scope.content}).success(function(data, status, headers, config) {
      $scope.messages.push(data);
      $scope.content = '';
      $ionicScrollDelegate.scrollBottom(true);
      $scope.loading = false;
    }).error(function(data, status, headers, config) {
      $scope.loading = false;
    });
  };

  $rootScope.hideTabs = true;
});