angular.module("app").controller('EventController', function($sce, $rootScope, $scope, $stateParams, $state, $window,
    $ionicActionSheet, $cordovaDevice, $ionicScrollDelegate, $ionicLoading, $timeout, EventResource) {

  $ionicLoading.show({ template: '加载中...' });

  $scope.showCustomBack = $stateParams.showCustomBack;
  // $scope.event = {};

  EventResource.get({ id: $stateParams.eventId }, function(event) {
    $scope.event = event;
    $scope.myEvent = (event.user_uuid === $window.localStorage.uuid);

    if ($scope.event.video_url && $scope.event.video_url.length > 0) {
      $scope.autoPlay = false;
      $scope.slideInterval = 10000;
    } else {
      $scope.autoPlay = true;
      $scope.slideInterval = 4000;
    }

    $timeout(function(){
      $ionicLoading.hide();
    }, 600);
  });

  // $scope.slideInterval = 4000;

  $scope.isEnd = function(time) {
    return Date.parse(time) < Date.now();
  };

  $timeout(function(){
    $ionicScrollDelegate.$getByHandle('eventScroll').scrollTop();
  },300);

  $scope.showSharesheet = function() {
    var text = "我刚刚在Owhat上发现了一个" + $scope.event.title + "活动，快来跟我一起O!!!";
    if ($scope.myEvent) {
      text = "我刚刚在Owhat上发起了一个" + $scope.event.title + "活动，快来跟我一起O!!!";
    }
    var message = {
      text: text,
      image: $scope.event.cover1,
      url: $scope.event.share_url
    };

    if ($cordovaDevice.getPlatform() !== "Android") {
      window.socialmessage.send(message);
    } else {
      var image = $scope.event.cover1;
      var url = $scope.event.share_url;
      appAvailability.share(text, image, url);
    }
    return;

    // var hideSheet = $ionicActionSheet.show({
    //   buttons: [
    //     {
    //       text: '<i class="icon-oShares icon-owxQuan"></i>微信朋友圈'
    //     },{
    //       text: '<i class="icon-oShares icon-owxFriend"></i>微信好友'
    //     },{
    //       text: '<i class="icon-oShares icon-oweibo"></i>新浪微博'
    //     },{
    //       text: '<i class="icon-oShares icon-oqqweibo"></i>腾讯微博'
    //     },{
    //       text: '<i class="icon-oShares icon-oqqKongjian"></i>QQ空间'
    //     }
    //   ],
    //   cancelText: '取消',
    //   cancel: function() {

    //   },
    //   buttonClicked: function(index) {
    //     return true;
    //   }
    // });
  };
});