angular.module("app").factory("UserResource", function($q, $resource) {
  return $resource(options.api.base_url + '/users/:id', {id: "@uuid"}, {
    follow: {method: 'POST', url: options.api.base_url + '/users/:id/follow'},
    unfollow: {method: 'POST', url: options.api.base_url + '/users/:id/unfollow'},
    followers: {method: 'GET', url: options.api.base_url + '/users/:id/followers'},
    login: {method: 'POST', url: options.api.base_url + '/auth/applogin'},
    logout: {method: 'DELETE', url: options.api.base_url + '/auth/sign_out'},
    updatePassword: {method: 'POST', url: options.api.base_url + '/users/update_password'},
    updateMobile: {method: 'POST', url: options.api.base_url + '/users/update_mobile'},
    search: {method: 'POST', url: options.api.base_url + '/users/search'}
  });
});