angular.module("app").config(function($stateProvider, $urlRouterProvider, $locationProvider) {

  $locationProvider.html5Mode(false);
  // $locationProvider.hashPrefix('!');

  $stateProvider.state('start', {
    url: '/start',
    abstract: true,
    templateUrl: 'start.html',
  }).state('start.login', {
    url: '/login',
    views: {
      'start': {
        templateUrl: 'login.html',
        controller: 'LoginController'
      }
    }
  }).state('start.register', {
    url: '/register',
    views: {
      'start': {
        templateUrl: 'register.html',
        controller: 'RegisterController'
      }
    }
  }).state('start.alipay', {
    url: '/alipay',
    views: {
      'start': {
        templateUrl: 'alipay.html',
        controller: 'AlipayController'
      }
    }
  }).state('start.success', {
    url: '/success',
    views: {
      'start': {
        templateUrl: 'success.html',
        controller: 'SuccessController'
      }
    }
  }).state('start.forget', {
    url: '/forget',
    views: {
      'start': {
        templateUrl: 'forget.html',
        controller: 'ForgetController'
      }
    }
  }).state('tabs', {
    url: '/tab',
    abstract: true,
    templateUrl: 'tabs.html',
  }).state('tabs.you', {
    url: '/you',
    views: {
      'you': {
        templateUrl: 'you.html',
        controller: 'YouController'
      }
    },
    access: {
      requiredAuthentication: true
    }
  }).state('tabs.friends', {
    url: '/friends',
    views: {
      'you': {
        templateUrl: 'friends.html',
        controller: 'FriendsController'
      }
    }
  }).state('tabs.me', {
    url: '/me',
    views: {
      'me': {
        templateUrl: 'me.html',
        controller: 'PersonController'
      }
    },
    access: {
      requiredAuthentication: true
    }
  }).state('tabs.hot', {
    url: '/hot',
    views: {
      'hot': {
        templateUrl: 'hot.html',
        controller: 'HotController'
      }
    }
  }).state('tabs.areas', {
    url: '/areas',
    views: {
      'me': {
        templateUrl: 'areas.html',
        controller: 'AreaController'
      }
    }
  }).state('tabs.cities', {
    url: '/areas/:id',
    views: {
      'me': {
        templateUrl: 'cities.html',
        controller: 'CityController'
      }
    }
  }).state('tabs.publish', {
    url: '/publish',
    views: {
      'me': {
        templateUrl: 'publish.html',
        controller: 'PublishController'
      }
    },
    resolve: {
      user: ['$http', '$window', 'MeResource', function($http, $window, MeResource) {
        return MeResource.get({ id: $window.localStorage.uuid }).$promise;
      }]
    }
  }).state('tabs.map', {
    url: '/map',
    views: {
      'me': {
        templateUrl: 'map.html',
        controller: 'MapController'
      }
    }
  }).state('tabs.hot_map', {
    url: '/event/:id/hot_map',
    views: {
      'hot': {
        templateUrl: 'map.html',
        controller: 'MapController'
      }
    }
  }).state('tabs.inbox', {
    url: '/inbox',
    views: {
      'me': {
        controller: 'InboxController',
        templateUrl: 'inbox.html'
      }
    }
  }).state('tabs.alerts', {
    url: '/alerts',
    views: {
      'me': {
        templateUrl: 'alerts.html',
        controller: 'AlertsController'
      }
    }
  }).state('tabs.messages', {
    url: '/messages',
    views: {
      'me': {
        templateUrl: 'messages.html',
        controller: 'MessagesController'
      }
    }
  }).state('tabs.tickets', {
    url: '/tickets',
    views: {
      'me': {
        templateUrl: 'tickets.html',
        controller: 'TicketsController'
      }
    }
  }).state('tabs.hot_tickets', {
    url: '/hot_tickets',
    views: {
      'hot': {
        templateUrl: 'tickets.html',
        controller: 'TicketsController'
      }
    }
  }).state('tabs.mobile', {
    url: '/mobile',
    views: {
      'me': {
        templateUrl: 'mobile.html',
        controller: 'MobileController'
      }
    }
  }).state('tabs.profile', {
    url: '/profile/:showCustomBack',
    views: {
      'me': {
        templateUrl: 'profile.html',
        controller: 'ProfileController'
      }
    }
  }).state('tabs.settings', {
    url: '/settings',
    views: {
      'me': {
        templateUrl: 'settings.html',
        controller: 'SettingsController'
      }
    },
    resolve: {
      user: ['$http', '$window', 'MeResource', function($http, $window, MeResource) {
        return MeResource.get({ id: $window.localStorage.uuid }).$promise;
      }]
    }
  });

  var sharedViews = ['follows', 'event', 'buy', 'checkout', 'person', 'comments', 'message', 'event_edit', 'attendees', 'event_map'];
  var routeName = '';
  var url = '';
  var templateName = '';
  var controllerName = '';
  var resolve = {};
  angular.forEach(sharedViews, function(view) {
    resolve = {};
    access = {};
    routeName = view;
    templateName = view;
    controllerName = view.toProperCase() + 'Controller';
    url = routeName;
    if (view === 'person') {
      templateName = 'me';
    }
    if (view === 'person' || view === 'message' || view === 'follows') {
      url = routeName + '/:userId';
    }
    if (view === 'attendees') {
      url = routeName + '/:eventId';
      resolve = {
        event: ['$http', '$stateParams', 'EventResource', function($http, $stateParams, EventResource) {
          return EventResource.get({ id: $stateParams.eventId }).$promise;
        }]
      };
    }
    if (view === 'event') {
      url = routeName + '/:eventId/:showCustomBack';
    }
    if (view === 'buy' || view === 'comments') {
      url = routeName + '/:eventId/:showCustomBack';
      resolve = {
        event: ['$http', '$stateParams', 'EventResource', function($http, $stateParams, EventResource) {
          return EventResource.get({ id: $stateParams.eventId }).$promise;
        }]
      };
    }
    if (view === 'buy' || view === 'comments' || view === 'attendees' || view === 'person') {
      access = { requiredAuthentication: true };
    }
    if (view === 'checkout') {
      url = routeName + '/:orderNo';
      resolve = {
        ticket: ['$http', '$stateParams', 'TicketResource', function($http, $stateParams, TicketResource) {
          return TicketResource.get({ orderNo: $stateParams.orderNo }).$promise;
        }]
      };
    }

    if (view === 'event_edit') {
      url = 'events/:eventId/edit';
      controllerName = 'EventEditController';
      resolve = {
        user: ['$http', '$window', 'MeResource', function($http, $window, MeResource) {
          return MeResource.get({ id: $window.localStorage.uuid }).$promise;
        }]
      };
    }

    if (view === 'event_map') {
      url = 'events/:eventId/map';
      controllerName = 'EventMapController';
      resolve = {
        event: ['$http', '$stateParams', 'EventResource', function($http, $stateParams, EventResource) {
          return EventResource.get({ id: $stateParams.eventId }).$promise;
        }]
      };
    }

    angular.forEach(['you', 'me', 'hot'], function(tab, index) {
      var stateUrl = tab + '_' + url;
      var views = {};
      views[tab] = {
        templateUrl: templateName + '.html',
        controller: controllerName
      };
      // console.log(stateUrl);
      $stateProvider.state('tabs.' + tab + '_' + routeName, {
        url: '/' + stateUrl,
        views : views,
        resolve: resolve,
        access: access
      });
    });
  });

  $urlRouterProvider.otherwise('/tab/hot');
});