angular.module("app").factory("EventResource", function($q, $resource) {
  return $resource(options.api.base_url + '/events/:id.json', {id : '@id'}, {
    attendees: {method: 'GET', url: options.api.base_url + '/events/:id/attendees'},
    export: {method: 'POST', url: options.api.base_url + '/events/:id/export', params: {email: "@email"}}
  });
});
