var options = {};
options.api = {};
options.api.base_url = "http://www.owhat.cn/api";
// options.api.base_url = "http://owhat.192.168.1.200.xip.io";
// options.api.base_url = "/api";

moment().lang("zh-cn");

var app = angular.module("app", ["ngResource", 'ionic', "ui.router", "ngCordova",
  "lr.upload", "mosFilters","baiduMap"]);

app.run(function($rootScope, $location, $window, AuthenticationService,
    $ionicTabsDelegate, $state, $ionicLoading, $ionicPopup, $ionicScrollDelegate, $interval, $cordovaDevice, $ionicPlatform) {

  $rootScope.hideTabs = false;

  // adds some basic utilities to the $rootScope for debugging purposes
  $rootScope.log = function(thing) {
    console.log(thing);
  };

  $rootScope.alert = function(thing) {
    alert(thing);
  };

  $rootScope.$on("$stateChangeStart", function(event, nextRoute, currentRoute) {
    //redirect only if both isAuthenticated is false and no token is set
    if (nextRoute != null && nextRoute.access != null && nextRoute.access.requiredAuthentication &&
      (!$window.localStorage.token || !$window.localStorage.uuid)) {
      $location.path("/start/login");
      return;
    }
    $rootScope.hideTabs = true;
    $ionicLoading.hide();

    if ($rootScope.intervalTimer) {
      $interval.cancel($rootScope.intervalTimer);
    }
  });

  $rootScope.$on("$stateChangeSuccess", function(event, toState, toParams) {
    $rootScope.hideTabs = true;

    if (window.socialmessag) {
      window.socialmessage.trackEvent({route: toState.name, params: JSON.stringify(toParams)});
    }
  });

  var tabPrefixs = ['you', 'hot', 'me'];

  $rootScope.goPerson = function(userId) {
    $state.go("tabs." + tabPrefixs[$ionicTabsDelegate.selectedIndex()] + "_person", {userId: userId});
  };
  $rootScope.goMessage = function(userId) {
    $state.go("tabs." + tabPrefixs[$ionicTabsDelegate.selectedIndex()] + "_message", {userId: userId});
  };
  $rootScope.goEvent = function(eventId) {
    $state.go("tabs." + tabPrefixs[$ionicTabsDelegate.selectedIndex()] + "_event", {eventId: eventId, showCustomBack: ""});
  };
  $rootScope.goAttendees = function(eventId) {
    $state.go("tabs." + tabPrefixs[$ionicTabsDelegate.selectedIndex()] + "_attendees", {eventId: eventId});
  };
  $rootScope.goEditEvent = function(eventId) {
    $state.go("tabs." + tabPrefixs[$ionicTabsDelegate.selectedIndex()] + "_event_edit", {eventId: eventId});
  };
  $rootScope.goEventMap = function(eventId) {
    $state.go("tabs." + tabPrefixs[$ionicTabsDelegate.selectedIndex()] + "_event_map", {eventId: eventId});
  };
  $rootScope.goBuy = function(eventId) {
    $state.go("tabs." + tabPrefixs[$ionicTabsDelegate.selectedIndex()] + "_buy", {eventId: eventId, showCustomBack: ""});
  };
  $rootScope.goCheckout = function(orderNo) {
    $state.go("tabs." + tabPrefixs[$ionicTabsDelegate.selectedIndex()] + "_checkout", {orderNo: orderNo});
  };
  $rootScope.goComments = function(eventId) {
    $state.go("tabs." + tabPrefixs[$ionicTabsDelegate.selectedIndex()] + "_comments", {eventId: eventId, showCustomBack: ""});
  };
  $rootScope.goTabHome = function(eventId) {
    $state.go("tabs." + tabPrefixs[$ionicTabsDelegate.selectedIndex()], {}, {reload: true});
  };
  $rootScope.goTabMe = function() {
    $state.go("tabs.me", {}, {reload: true});
  };
  $rootScope.goTabProfile = function() {
    $state.go("tabs.profile", {}, {reload: true});
  };
  $rootScope.goFollows = function(userId,type) {
    $rootScope.follow_type = type;
    $state.go("tabs." + tabPrefixs[$ionicTabsDelegate.selectedIndex()] + "_follows", {userId: userId});
  };
  $rootScope.goMap = function() {
    $state.go("tabs.map", {}, {location: 'replace'});
  };

  $rootScope.popupNotice = function(message){
    var publishPopup = $ionicPopup.show({
      template: '<div class="forget-popup text-center"><div>' + message + '</div></div>',
      title: false,
      buttons: [
        {
          text: '确认',
          type: 'btn-popup-sm btn-green-dark',
          onTap: function(e) {
            //
          }
        }
      ]
    });
  };
});

app.config(function ($httpProvider, $sceDelegateProvider) {
  $httpProvider.interceptors.push('TokenInterceptor');

  $sceDelegateProvider.resourceUrlWhitelist([
     // Allow same origin resource loads.
     'self',
     // Allow loading from our assets domain.  Notice the difference between * and **.
     'http://player.youku.com/**'
   ]);
});

app.controller('PopupForget',function($scope, $ionicPopup, $timeout) {

// Triggered on a button click, or some other target
  $scope.verifyPopupForget = function() {
    $scope.data = {};
    // 不明物体鉴定中...
    var verifyPopup = $ionicPopup.show({
      template: '<div class="forget-popup"><img class="user-photo" src="img/icon-question.png" alt="" /> 不明物体鉴定中...</div>',
      title: '',
      subTitle: '',
      scope: $scope,
      buttons: ''
    });
    verifyPopup.then(function(res) {
      // console.log('Tapped!', res);
      // 重置成功
      var resultPopup = $ionicPopup.show({
        template: '<div class="forget-popup">' +
        '<img class="user-photo pull-left" src="img/icon-ghost-gray.png" alt="" />' +
        '<div class="messages">' +
        '<span class="assertive block">复活啦！</span>' +
        '请牢记新暗号' +
        '</div>' +
        '</div>',
        title: '',
        subTitle: '',
        scope: $scope,
        buttons: ''
      });
      $timeout(function() {
        resultPopup.close(); //close the popup after 3 seconds for some reason
      }, 3000);
    });
    $timeout(function() {
      verifyPopup.close(); //close the popup after 3 seconds for some reason
    }, 3000);
  };
});

String.prototype.toProperCase = function () {
  return this.replace(/\w\S*/g, function(txt){return txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase();});
};
