angular.module("app").controller('FollowsController', function(EventResource, $scope, $rootScope, $stateParams, $location, UserResource, $window, $http) {
  $scope.followers = [];

  $scope.isEmpty = false;
  $scope.haveMore = true;
  $scope.pagination = {perSize: 20, currentPage: 1};

  $scope.moreDataCanBeLoaded = function(){
    return $scope.haveMore;
  };

  $scope.fetchMore = function(){
    if ($scope.fetchMoreIng) {
      return;
    }

    $scope.fetchMoreIng = true;
    UserResource.followers({id: $stateParams.userId, page: 1, per: 30,type: $rootScope.follow_type}, function(data){
      $scope.totalItems = data.count;
      $scope.followers.push.apply($scope.followers, data.followers);
      $window.localStorage.unreadFollowers = 0;

      $scope.$broadcast('scroll.infiniteScrollComplete');
      $scope.haveMore = false;
      $scope.haveMore = $scope.pagination.currentPage * $scope.pagination.perSize < data.count;
      $scope.pagination.currentPage += 1;
      $scope.fetchMoreIng = false;

      $scope.isEmpty = ($scope.totalItems.length === 0);
    }, function() {
      $scope.$broadcast('scroll.infiniteScrollComplete');
      $scope.fetchMoreIng = false;
    });
  };

  $scope.imageWidth = window.innerWidth;
  $scope.imageHeight = 58;

  $scope.isMe = function(uuid) {
    return uuid === $window.localStorage.uuid;
  };

  $scope.unfollow = function(user, index){
    $http.post(options.api.base_url + '/users/' + user.uuid + '/unfollow').success(function(user) {
      $scope.followers[index] = user;
    }).error(function(error){

    });
  };

  $scope.follow = function(user, index){
    $http.post(options.api.base_url + '/users/' + user.uuid + '/follow').success(function(user) {
      $scope.followers[index] = user;
    }).error(function(error){

    });
  };
});