angular.module("app").controller('BuyController', function(event, $state, $ionicNavBarDelegate, $rootScope, $scope, $stateParams, $ionicPopup, $ionicLoading, $window, TicketResource, MeResource) {
  $rootScope.hideTabs = true;

  $scope.saveTicketDate = function(data){
    $window.localStorage['ticketInfo:' + $window.localStorage.uuid] = JSON.stringify(data);
  };

  $scope.goBack = function(){
    var data = {};
    if ($scope.ticket.name) {
      data.ticket_name = $scope.ticket.name;
    }
    if ($scope.ticket.phone) {
      data.ticket_phone = $scope.ticket.phone;
    }
    if ($scope.ticket.email) {
      data.ticket_email = $scope.ticket.email;
    }
    if ($scope.ticket.gender) {
      data.ticket_gender = $scope.ticket.gender;
    }

    $scope.saveTicketDate(data);
    $ionicNavBarDelegate.back();
  };

  $scope.event = event;

  MeResource.get(function(user) {
    var data = {};
    try {
      var uuid = window.localStorage.uuid;
      var key = 'ticketInfo:' + uuid;
      data = window.localStorage[key];
      data = JSON.parse(data);
    } catch (e) {
      return;
    }

    if (data.ticket_name) {
      $scope.ticket.name = data.ticket_name;
    } else {
      $scope.ticket.name = user.nickname;
    }

    if (data.ticket_phone) {
      $scope.ticket.phone = data.ticket_phone;
    } else {
      $scope.ticket.phone = user.mobile;
    }

    if (data.ticket_email) {
      $scope.ticket.email = data.ticket_email;
    }

    if (data.ticket_gender) {
      $scope.ticket.gender = data.ticket_gender;
    } else {
      if (user.sex === true) {
        $scope.ticket.gender = '1';
      } else {
        $scope.ticket.gender = '0';
      }
    }
  });

  var ticket = $scope.ticket = new TicketResource({gender: '1'});

  $scope.checked = false;

  $scope.showConfirm = function() {
    var message = false;

    if (event.reg_fields.indexOf('name') > -1 && !ticket.name) {
      message = "请输入姓名";
    } else if (event.reg_fields.indexOf('phone') > -1 && !ticket.phone) {
      message = "请输入正确的手机号码";
    } else if (event.reg_fields.indexOf('email') > -1 && !ticket.email) {
      message = "请输入正确的邮箱";
    } else if (event.reg_fields.indexOf('gender') > -1 && !ticket.gender) {
      message = "请输入性别";
    } else if ($scope.event.ext_info && !$scope.ticket.ext_info) {
      message = "请输入" + $scope.event.ext_info;
    } else if (ticket.quantity <= 0) {
      message = "选择正确的票数";
    }

    if (message) {
      var alertPopup = $ionicPopup.alert({
        template: '<div class="popup-buttons">' + message + '</div>',
        buttons: [
        {
          text: '确定',
          type: 'button btn-green-dark btn-popup-sm'
        }
        ]
      });

      alertPopup.then(function(res) {
        $scope.checked = true;
      });

    } else {
      var confirmPopup = $ionicPopup.confirm({
        template: '<div class="popup-buttons">请确认您要提交的内容，如无需变更，请点击“确认”</div>',
        buttons: [
        {
          text: '取消',
          type: 'button btn-green-dark btn-popup-sm'
        },{
          text: '确认',
          type: 'button btn-green-dark btn-popup-sm',
          onTap: function(e){
            $ionicLoading.show({ template: '加载中...' });
            $scope.ticket.event_id = $stateParams.eventId;
            var data = {};
            data.ticket_name = $scope.ticket.name;
            data.ticket_phone = $scope.ticket.phone;
            data.ticket_email = $scope.ticket.email;
            data.ticket_gender = $scope.ticket.gender;
            $scope.saveTicketDate(data);

            $scope.ticket.$save(function(data){
              if (event.fee === null || event.fee === 0) {
                  $ionicPopup.alert({
                    template: '<div class="popup-buttons"><center>报名成功</center></div>',
                    okText: '确定',
                    okType: 'button btn-green-dark btn-popup-sm'
                  });
                $rootScope.goEvent(event.id);
              } else {
                $rootScope.goCheckout(data.order_no);
              }

            }, function(error){
              $ionicLoading.hide();
              var alertPopup = $ionicPopup.alert({
                template: '服务器出错啦',
                okText: '确定',
                okType: 'button btn-green-dark btn-popup-sm'
              });

              alertPopup.then(function(res) {
                console.log(error.data);
              });
            });
          }
        }
        ]
      });
    }
  };

  $scope.addTicketNumber = function(){
    if ((event.ticket_limit == null || event.ticket_limit === 0 || ticket.quantity < event.ticket_limit) && (event.ticket_total == null || ticket.quantity < event.ticket_total)) {
      ticket.quantity++;
    }
  };
  $scope.minusTicketNumber = function(){
    if (ticket.quantity > 1) {
      ticket.quantity--;
    }
  };
});
