app.directive('youku', function($sce, $window) {
    return {
        restrict: 'E',
        template: '<iframe id="youkuPlayer" width="100%" height=224 ng-src="{{url}}" frameborder=0 allowfullscreen></iframe>',
        replace: true,
        transclude: true,
        link: function(scope, element, attributes) {
            var vid =  attributes.url.split('/id_')[1].split('.html')[0];
            scope.url = "http://player.youku.com/embed/" + vid;
        }
    };
});