angular.module("app").controller('AlertsController', function($window, $scope, $location, AlertRescource,$ionicPopup,$ionicLoading) {
  AlertRescource.get(function(data){
    $scope.messages = data.activities;
    $window.localStorage.unreadActivities = 0;
  });

  $scope.deleteNotify = function($index){
    var id = this.msg.id;
    var confirmPopup = $ionicPopup.confirm({
      template: '<div class="popup-buttons" style="text-align:center;color:#ff4128">确认删除</div>',
      buttons: [
      {
        text: '取消',
        type: 'button btn-green-dark btn-popup-sm'
      },{
        text: '确认',
        type: 'button btn-green-dark btn-popup-sm',
        onTap : function(e){
        //trackable_id
        AlertRescource.delete({id:id}).$promise.then(function(){
          var _remove = $scope.messages.splice($index,1);
        },function(err){
          console.error(err);
        });
      }
    }
    ]
    });
  };

  $scope.showConfirm = function() {
    var message = false;

    if (message) {
      var alertPopup = $ionicPopup.alert({
        template: '<div class="popup-buttons">' + message + '</div>',
        buttons: [
        {
          text: '确定',
          type: 'button btn-green-dark btn-popup-sm'
        }
        ]
      });

      alertPopup.then(function(res) {
        $scope.checked = true;
      });

    } else {
      var confirmPopup = $ionicPopup.confirm({
        template: '<div class="popup-buttons" style="text-align:center;color:#ff4128">确认删除</div>',
        buttons: [
        {
          text: '取消',
          type: 'button btn-green-dark btn-popup-sm'
        },{
          text: '确认',
          type: 'button btn-green-dark btn-popup-sm'
        }
        ]
      });
    }
  };


});