angular.module("app").controller('InboxController', function($window, $scope) {
  $scope.userId = $window.localStorage.uuid;
  $scope.hasUnreadMessage = $window.localStorage.unreadMessages > 0;
  $scope.hasUnreadActivity = $window.localStorage.unreadActivities > 0;
  $scope.hasUnreadFollower = $window.localStorage.unreadFollowers > 0;
});