angular.module("app").controller('LoginController', function($ionicPopup, $scope, $location, $rootScope, UserResource,
    $state, $window, $http, $ionicLoading, $cordovaDevice) {
  $scope.user = {};

  if ($window.localStorage.uuid && $window.localStorage.token) {
    $state.go("tabs.hot");
    return;
  } else {
    delete $window.localStorage.token;
    delete $window.localStorage.uuid;
  }


  $scope.submitForm = function() {
    var user = new UserResource();
    user.login = $scope.user.login;
    user.password = $scope.user.password;
    user.$login().then(function() {
      $window.localStorage.uuid = user.uuid;
      $window.localStorage.token = user.token;
      $state.go("tabs.hot");
      return;
    }, function() {
      // alert("出错啦！");
      $rootScope.popupNotice("请检查您的用户名和密码");
    });
  };

  $scope.parser = document.createElement('a');
  var oauthLogin = function (provider) {
    var url = "";
    if (provider === 'weibo') {
      url = "https://api.weibo.com/oauth2/authorize?client_id=337167425&response_type=code&redirect_uri=http%3A%2F%2Fwww.owhat.cn/auth/weibo/callback404&state=app";
    } else {
      url = "https://graph.qq.com/oauth2.0/authorize?client_id=101138496&redirect_uri=http%3A%2F%2Fwww.owhat.cn/404.html&response_type=code&state=app";
    }

    var ref = window.open(url, '_blank', 'location=no,closebuttoncaption=Close');
    ref.addEventListener('loadstart', function(event) {
      // alert(event.url);
      if (event.url.indexOf('http://www.owhat') === 0) {
        ref.close();
        $scope.parser.href = event.url;
        $scope.$apply(function(scope){
          code = $scope.parser.search.split("code=")[1].split("&")[0];
          if (code) {
            var callback_url = options.api.base_url + '/auth/' + provider + '/callback?state=app&code=' + code;
            $http.get(callback_url).success(function(user) {
              if (user && user.uuid) {
                $window.localStorage.uuid = user.uuid;
                $window.localStorage.token = user.token;
                $state.go("tabs.hot");
              }
            }).error(function() {
              $ionicPopup.alert({
                template: '<div class="popup-buttons">出错啦！</div>',
                buttons: [
                  {
                    text: '确定',
                    type: 'button btn-green-dark btn-popup-sm'
                  }
                ]
              });
            });
          }
        });
      }
    });
  };

  $scope.weiboLogin = function() {
    if ($cordovaDevice.getPlatform() !== "Android") {
      if (window.canOpenURL) {
        window.canOpenURL.check(function(ok) {
          if (ok === 0) {
            oauthLogin('weibo');
          } else {
            $ionicLoading.show({ template: '加载中...' });
            window.socialmessage.weiboLogin(function(data) {
              var uid = data.usid;
              var token = data.accessToken;
              var tokenUrl = options.api.base_url + '/auth/weibo/save_token';
              $http.post(tokenUrl, {uid: uid, token: token}).success(function(user) {
                $window.localStorage.uuid = user.uuid;
                $window.localStorage.token = user.token;
                $state.go("tabs.hot");
              }).error(function(error) {
                $ionicLoading.hide();
              });
            }, function(error) {
              $ionicLoading.hide();
            });
          }
        }, function(error) {
          $ionicPopup.alert({
            template: '<div class="popup-buttons">出错啦！</div>',
            buttons: [
              {
                text: '确定',
                type: 'button btn-green-dark btn-popup-sm'
              }
            ]
          });
        }, "weibo://");
      }
    } else {
      appAvailability.check("com.sina.weibo", function() {
        appAvailability.weiboLogin(
          function(result) {           // Success callback
            $ionicLoading.show({ template: '加载中...' });
            // alert(result.uid);
            // alert(result.token);
            var uid = result.uid;
            var token = result.token;
            var tokenUrl = options.api.base_url + '/auth/weibo/save_token';
            // alert(uid);
            // alert(token);
            $http.post(tokenUrl, {uid: uid, token: token}).success(function(user) {
              $window.localStorage.uuid = user.uuid;
              $window.localStorage.token = user.token;
              $state.go("tabs.hot");
            }).error(function(error) {
              $ionicLoading.hide();
            });
          },
          function() {           // Error callback
            // alert('Twitter is not available');
            $ionicLoading.hide();
          }
        );
      }, function() {
        oauthLogin('weibo');
      });
    }
  };

  $scope.qqLogin = function() {
    if ($cordovaDevice.getPlatform() !== "Android") {
      if (window.canOpenURL) {
        window.canOpenURL.check(function(ok) {
          if (ok === 0) {
            oauthLogin('qq_connect');
          } else {
              $ionicLoading.show({ template: '加载中...' });
              window.socialmessage.qqLogin(function(data) {
                var uid = data.usid;
                var token = data.accessToken;
                var tokenUrl = options.api.base_url + '/auth/qq_connect/save_token';
                // alert(uid);
                // alert(token);
                $http.post(tokenUrl, {uid: uid, token: token}).success(function(user) {
                  $window.localStorage.uuid = user.uuid;
                  $window.localStorage.token = user.token;
                  $state.go("tabs.hot");
                }).error(function(error) {
                  $ionicLoading.hide();
                });
              }, function(error) {
                $ionicLoading.hide();
              });
          }
        }, function(error) {
          // alert("this is error");
          alert(error);
        }, "mqqOpensdkSSoLogin://");
      }
    } else {
      appAvailability.qqLogin(
        function(result) {           // Success callback
          $ionicLoading.show({ template: '加载中...' });
          // alert(result.uid);
          // alert(result.token);
          var uid = result.uid;
          var token = result.token;
          var tokenUrl = options.api.base_url + '/auth/qq_connect/save_token';
          // alert(uid);
          // alert(token);
          $http.post(tokenUrl, {uid: uid, token: token}).success(function(user) {
            $window.localStorage.uuid = user.uuid;
            $window.localStorage.token = user.token;
            $state.go("tabs.hot");
          }).error(function(error) {
            $ionicLoading.hide();
          });
        },
        function() {           // Error callback
          // alert('Twitter is not available');
          $ionicLoading.hide();
        }
      );
    }
  };
});
