angular.module("app").controller('YouController', function($timeout, $scope, $rootScope, $ionicPopup, $location, EventResource, $window, $ionicLoading) {
  $rootScope.hideTabs = false;
  $scope.haveMore = true;
  $scope.events = [];
  $scope.pagination = {perSize: 20, currentPage: 1};

  $scope.imageWidth = window.innerWidth;
  $scope.imageHeight = $scope.imageWidth / 320 * 223;

  userId = $window.localStorage.uuid;

  $scope.moreDataCanBeLoaded = function(){
    return $scope.haveMore;
  };

  $scope.fetchMore = function(){
    EventResource.get({user_id: userId, following: 1, per: $scope.pagination.perSize, page: $scope.pagination.currentPage}, function(data) {
      for (var i = 0;  i < data.events.length; i++ ){
        $scope.events.push(data.events[i]);
      }

      $scope.$broadcast('scroll.infiniteScrollComplete');
      $scope.haveMore = false;
      $scope.haveMore = $scope.pagination.currentPage * $scope.pagination.perSize < data.count;
      $scope.pagination.currentPage += 1;
    }, function() {
      $scope.$broadcast('scroll.infiniteScrollComplete');
    });
  };

  $scope.doRefresh = function() {
    $scope.pulling = true;

    EventResource.get({user_id: userId, following: 1, per: $scope.pagination.perSize, page: 1}, function(data) {
      $scope.events = data.events;
      $scope.$broadcast('scroll.refreshComplete');
      $scope.pulling = false;
      $scope.pagination.currentPage = 2;
      $scope.haveMore = true;
    }, function() {
      $scope.$broadcast('scroll.refreshComplete');
      $scope.pulling = false;
      $scope.haveMore = true;
    });
  };
});