angular.module("app").controller('AlipayController', function($scope, $stateParams, $rootScope, TicketResource) {
  $scope.parser = document.createElement('a');
  $scope.isOnDevice = ionic.Platform.isIOS() || ionic.Platform.isAndroid();
  $scope.ticket = TicketResource.get({order_no: $stateParams.order_no });

  $scope.goAlipay = function(url) {
    var ref = window.open(url, '_blank', 'location=no');
  };

});