angular.module("app").controller('HotController', function($scope, $rootScope, $location, EventResource,
    $timeout, $ionicLoading, $cordovaPush, $http, $window, $ionicScrollDelegate, $cordovaDevice, $ionicPopup) {
  if (navigator.splashscreen) {
    navigator.splashscreen.hide();
  }

  if ($window.localStorage.uuid) {
    $scope.signedIn = true;
  } else {
    $scope.signedIn = false;
  }

  $rootScope.hideTabs = false;
  $scope.pulling = false;
  $scope.haveMore = true;
  $scope.pagination = {perSize: 20, currentPage: 1};
  $scope.events = [];
  $scope.fetchMoreIng = false;

  var delegate = $ionicScrollDelegate.$getByHandle('hotScroll');
  delegate.rememberScrollPosition('hotScroll');

  $scope.imageWidth = window.innerWidth;
  $scope.imageHeight = $scope.imageWidth / 320 * 223;

  $scope.moreDataCanBeLoaded = function(){
    return $scope.haveMore;
  };

  $scope.fetchMore = function(){
    if ($scope.fetchMoreIng) {
      return;
    }

    $scope.fetchMoreIng = true;
    EventResource.get({per: $scope.pagination.perSize, page: $scope.pagination.currentPage}, function(data) {
      $scope.events.push.apply($scope.events, data.events);

      if ($scope.pagination.currentPage === 1) {
        delegate.scrollToRememberedPosition();
      }

      $scope.$broadcast('scroll.infiniteScrollComplete');
      $scope.haveMore = false;
      $scope.haveMore = $scope.pagination.currentPage * $scope.pagination.perSize < data.count;
      $scope.pagination.currentPage += 1;
      $scope.fetchMoreIng = false;
    }, function() {
      $scope.$broadcast('scroll.infiniteScrollComplete');
      $scope.fetchMoreIng = false;
    });
  };

  $scope.fetchMore();

  $scope.doRefresh = function() {
    $scope.pulling = true;

    EventResource.get({per: $scope.pagination.perSize, page: 1}, function(data) {
      $window.localStorage.hot_events = $scope.events = data.events;
      $scope.$broadcast('scroll.refreshComplete');
      $scope.pulling = false;
      $scope.pagination.currentPage = 2;
      $scope.haveMore = true;
    }, function() {
      $scope.$broadcast('scroll.refreshComplete');
      $scope.pulling = false;
      $scope.haveMore = true;
    });
  };

  function onNotificationAPN (event) {
    if ( event.alert ) {
      navigator.notification.alert(event.alert);
    }
  }

  if (window.plugins && window.plugins.pushNotification && $window.localStorage.uuid && $window.localStorage.aps_token_posted !== true) {
    var iosConfig = {
      "badge":"true",
      "sound":"true",
      "alert":"true",
      "ecb":"onNotificationAPN"
    };

    $cordovaPush.register(iosConfig).then(function(result) {
      $http.post(options.api.base_url + '/users/' + $window.localStorage.uuid + '/aps_token', {aps_token: result}).
        success(function(data, status, headers, config) {
          $window.localStorage.aps_token_posted = true;
        }).
        error(function(data, status, headers, config) {
          // called asynchronously if an error occurs
          // or server returns response with an error status.
        });
    }, function(err) {
      // alert(err);
      // An error occured. Show a message to the user
    });
  }

  if (!$rootScope.checkUpdateFlag) {
    if (ionic.Platform.isAndroid()) {
      $timeout(function() {
        appAvailability.checkUpdate();
      }, 1000);
    }
    $rootScope.checkUpdateFlag = true;
  }
});